
import React, {useState, useEffect} from 'react'
import styled from 'styled-components';
import * as S from '../styles/Majorcss';

//
import {useSelector, useDispatch} from "react-redux"
import {addTitle, ChangeState} from '../redux/store'
import { Navigate } from "react-router-dom";



function Mainpage() {
    const [inputvalue, setinputvalue] = useState('')

    //
    let a = useSelector((state) => { return state } )
    let dispatch = useDispatch()
    //a.user.is_done = false

     //  is_done false 
    useEffect(() => {
        dispatch(ChangeState())
    });

    return (
        <S.Wrapper>
        {console.log(a.user)}
        <S.Text>  <br/> </S.Text>
        <S.Put value = {inputvalue} type="text" placeholder="" onChange={(event)=>setinputvalue(event.target.value)}/>
        <S.InputLine/>
        <S.BigButton onClick={() => (dispatch(addTitle(inputvalue)))}></S.BigButton>
        {a.user.is_done && (<Navigate to="/category"/> )}   
        </S.Wrapper>
    )
}

export default Mainpage
