
import React, {useState, useEffect} from 'react'
import styled from 'styled-components'
import * as S from '../styles/Majorcss'

//
import {useSelector, useDispatch} from "react-redux"
import {addNickname, ChangeState} from '../redux/store'
import { Navigate } from "react-router-dom";


function Mainpage() {
    const [inputvalue, setinputvalue] = useState('')

    //
    let a = useSelector((state) => { return state } )
    let dispatch = useDispatch()
    
     //  is_done false 
    useEffect(() => {
        dispatch(ChangeState())
    });
    return (
        <S.Wrapper>
            {console.log(a.user)}
            <S.Text> </S.Text>
            <div class="margin">
            <S.Put value = {inputvalue} type="text" placeholder="" onChange={(event)=>setinputvalue(event.target.value)}/>
            <S.InputLine/>
            </div>
            <S.BigButton onClick={() => (dispatch(addNickname(inputvalue)))}></S.BigButton>
            {a.user.is_done && (<Navigate to="/title"/> )}  
        </S.Wrapper>
    )
}

export default Mainpage

