
import React, {useState} from 'react'
import styled from 'styled-components';
import * as S from '../styles/Majorcss';
import "../styles/main.css"
import tree from '../img/tree.png';
import axios from 'axios';

import Footer from '../components/Footer/Footer';

const MainButton = styled.button`
background: #FFFFFF;
border: 1px solid #B9ADFF;
box-shadow: 0px 0px 15px rgba(106, 138, 255, 0.1);
border-radius: 18px;

width: 300px;
height: 50px;
position: flex;
bottom: 0;
margin-bottom: 10px;
display: inline-block;

//
font-family: 'Pretendard';
font-style: normal;
font-weight: 700;
font-size: 17px;
line-height: 12px;
color: #8571FF;

`


function Mainpage() {

    const [DB, setDB]= useState([]);
    
    const getDate = async()=>{
        try{
            const res = await axios.get(
                'http://127.0.0.1:8000/',
            );
            setDB(res.data);
            //console.log(res.data);
        }catch(e){
            console.log(e)
        }
        
        
    const cnt = DB.length;
    const per = cnt * 10;
    console.log(cnt);
    document.querySelector(".countP").innerHTML = cnt;
    document.querySelector(".progress-level").style.width = per + "%";
    }
    return (
        <>
            <S.Wrapper>
                <S.Text>
                      2023 , 
                    <br/>  
                    <br/> 
                </S.Text>
                <div class="progress" onWaiting={getDate()}>
                    <div class="progress-level" ></div>
                </div>
                <S.textP>
                       &nbsp;
                    <p class="countP">100</p>
                    
                </S.textP>
                <div>
                    <img class="tree" src={tree} ></img>
                </div>

                <MainButton onClick={() => (window.location.href = '/nickname')}> </MainButton> 
            </S.Wrapper>
        </>
    )
}

export default Mainpage
