import React, {useState, useEffect} from 'react'
import styled from 'styled-components';
import * as S from '../styles/Majorcss';

//
import {useSelector, useDispatch} from "react-redux"
import {addEmail, ChangeState} from '../redux/store'
import { Navigate } from "react-router-dom";

const Text = styled.div`
display: inline-block;
position: left;
text-align: left;
margin-left: 3px;
font-family: 'Pretendard';
font-style: normal;
font-weight: 500;
font-size: 15px;
line-height: 30px;
letter-spacing: 0.035em;

color: #8571FF;
`

function Mainpage() {
    const [inputvalue, setinputvalue] = useState('')

     //
    let a = useSelector((state) => { return state } )
    let dispatch = useDispatch()
    
    //  is_done false 
    useEffect(() => {
        dispatch(ChangeState())
    });

    return (
        <S.Wrapper>
        {console.log(a.user)}
        <S.Text>  </S.Text>
        <Text>2023.01.01 ,  .</Text>
        <S.Put value = {inputvalue} type="text" placeholder=" " onChange={(event)=>setinputvalue(event.target.value)}/>
        <S.InputLine/>
        <S.BigButton onClick={() => (dispatch(addEmail(inputvalue)))}></S.BigButton>
            {a.user.is_done && (<Navigate to="/complete"/> )} 
        </S.Wrapper>
    )
}

export default Mainpage